import { axios } from './axios'
// 获取播放记录
export function getRecord(uid, type) {
  return axios({
    url: '/user/record',
    params: {
      uid,
      type
    }
  })
}
// 获取用户歌单
export function getUserPlayList(uid, limit, offset) {
  return axios({
    url: '/user/playlist',
    params: {
      uid,
      limit,
      offset
    }
  })
}
// 获取用户关注列表
export function getFollows(uid, limit, offset) {
  return axios({
    url: '/user/follows',
    params: {
      uid,
      limit,
      offset
    }
  })
}
// 获得粉丝列表
export function getFolloweds(uid, limit, offse) {
  return axios({
    url: '/user/followeds',
    params: {
      uid,
      limit,
      offse
    }
  })
}
