<template>
  <div id="profile">
    <div class="bg">
      <div class="info">
        <img :src="userDetail.avatarUrl" alt="" />
        <div class="name">{{ userDetail.nickname }}</div>
        <div class="resources">
          <div class="follows">
            <div class="num">{{ userDetail.follows }}</div>
            <p>关注</p>
          </div>
          <i class="line"></i>
          <div class="followeds">
            <div class="num">{{ userDetail.followeds }}</div>
            <p>粉丝</p>
          </div>
        </div>
      </div>
    </div>
    <div class="header limit">
      <ul class="nav">
        <li v-for="item in nav" :key="item.id" @click="active(item.id)" :class="{ active: id === item.id }">{{ item.name }}</li>
      </ul>
      <div class="like" v-if="id === 1">
        <ul class="item">
          <li v-for="item in like" :key="item.id" @click="itemActive(item.id)" :class="{ active: itemActiveId === item.id }">{{ item.name }}</li>
        </ul>
        <songs-list v-if="itemActiveId === 1" :songs="recordSong"></songs-list>
        <song-sheet v-if="itemActiveId === 2" :songSheet="collectSheet"></song-sheet>
      </div>
      <div class="my-sheet" v-if="id === 2">
        <song-sheet :songSheet="mySongSheet"></song-sheet>
      </div>
      <div class="follows" v-if="id === 3">
        <follows :follows="follows"></follows>
      </div>
       <div class="follows" v-if="id === 4">
        <follows :follows="followeds"></follows>
      </div>
    </div>
  </div>
</template>

<script>
import { getUserDetail } from '@/network/login'
import { getRecord, getUserPlayList, getFollows ,getFolloweds} from '@/network/profile'
// 处理歌曲信息
import { SongsInfo } from '@/network/detail'
import songsList from '@/components/common/ArtistList/ArtistList'
import songSheet from '@/components/common/songSheet/songSheet'
import follows from '@/components/content/folllows/follows'

import { mapMutations, mapGetters } from 'vuex'

export default {
  components: {
    songsList,
    songSheet,
    follows
  },
  data() {
    return {
      userDetail: {},
      id: 1,
      itemActiveId: 1,
      // 播放纪律
      recordSong: [],
      mySongSheet: {},
      collectSheet: {},
      follows:[],
      followeds:[],

      like: [
        {
          name: '播放记录',
          id: 1
        },
        {
          name: '歌单',
          id: 2
        },
        {
          name: '粉丝',
          id: 3
        }
      ],
      nav: [
        {
          name: '我喜欢',
          id: 1
        },
        {
          name: '我创建的歌单',
          id: 2
        },
        {
          name: '关注',
          id: 3
        },
        {
          name: '粉丝',
          id: 4
        }
      ]
    }
  },
  created() {},
  mounted() {
    let id = sessionStorage.getItem('id')
    console.log(id)
    this.init(id)
  },
  computed: {
    ...mapGetters(['loginState', 'userInfo'])
  },
  methods: {
    init(id) {
      this.getUserDetail(id)
      this.getRecord(id)
      this.getUserPlayList(id)
      this.getFollows(id)
      this.getFolloweds(id)
    },
    // 获取用户信息
    getUserDetail(id) {
      getUserDetail(id).then(res => {
        let userInfo = res.data.profile
        userInfo.level = res.data.level
        userInfo.createDays = res.data.createDays
        userInfo.createTime = res.data.createTime
        userInfo.listenSongs = res.data.listenSongs
        this.userDetail = userInfo
        console.log(this.userDetail)
      })
    },
    // 获取播放纪律
    getRecord(id) {
      getRecord(id).then(res => {
        let data = []
        let recordSong = []
        res.data.allData.forEach(item => {
          data.push(item)
        })
        data.forEach(item => {
          recordSong.push(new SongsInfo(item.song))
        })
        this.recordSong = recordSong
      })
    },
    // 获取用户歌单
    getUserPlayList(id) {
      getUserPlayList(id).then(res => {
        let mySongSheet = []
        let collectSheet = []
        res.data.playlist.forEach(item => {
          if (item.userId == id) {
            mySongSheet.push(item)
          } else {
            collectSheet.push(item)
          }
        })
        this.mySongSheet = mySongSheet
        this.collectSheet = collectSheet
      })
    },
    // 获得用户关注
    getFollows(id) {
      getFollows(id).then(res => {
        this.follows=res.data.follow
      })
    },
    // 获得粉丝
    getFolloweds(id){
      getFolloweds(id).then(
        res=>{
          this.followeds=res.data.followeds
          console.log(res);
        }
      )
    },

    active(id) {
      this.id = id
    },
    itemActive(id) {
      this.itemActiveId = id
    }
  }
}
</script>

<style scoped lang="less">
#profile {
  .bg {
    position: relative;
    height: 380px;
    background: url('../../assets/img/profile/bg_profile.jpg') no-repeat;
    background-size: 100% 100%;
    .info {
      display: flex;
      flex-direction: column;
      align-items: center;
      position: absolute;
      top: 50%;
      left: 50%;
      transform: translate(-50%, -50%);
      img {
        width: 120px;
        height: 120px;
        border-radius: 50%;
        padding: 5px;
        background-color: #fff;
        margin-bottom: 10px;
      }
      .name {
        color: #fff;
        font-size: 18px;
      }
      .resources {
        display: flex;
        justify-content: center;
        align-items: center;
        color: #fff;
        font-size: 18px;
        .line {
          width: 0.5px;
          height: 35px;
          background-color: #666;
        }
        .follows,
        .followeds {
          text-align: center;
          margin: 30px 30px;
          font-size: 14px;
          .num {
            font-size: 22px;
            font-weight: 400;
            margin-bottom: 5px;
          }
          .num:hover {
            color: #31c27c;
          }
        }
      }
    }
  }
  .active {
    color: #31c27c;
  }
  .header {
    min-height: 500px;
    margin-top: -56px;
    .nav {
      position: relative;
      width: 1200px;

      display: flex;
      color: #fff;
      li {
        line-height: 56px;
        margin-right: 35px;
      }
      li:hover {
        color: #31c27c;
      }
    }
    .like {
      .item {
        display: flex;
        li {
          line-height: 56px;
          margin-right: 35px;
        }
      }
    }
  }
}
</style>
